import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    redirectTo: '/home',
    pathMatch: 'full',
  },
  {
    path: 'home',
    loadChildren: () =>
      import('./Home/home-module/home-module.module').then(
        (r) => r.HomeModuleModule
      ),
  },
  {
    path: 'login',
    loadChildren: () =>
      import('./authModule/auth/auth.module').then((r) => r.AuthModule),
  },
  {
    path: 'ProductDetail',
    loadChildren: () =>
      import('./product/product.module').then((r) => r.ProductModule),
  },
  // { path: '**', redirectTo: '/home/homePage/', pathMatch: 'full' },

  // {
  //   path:"**",
  //   component: PageNotFoundComponent
  // }
];
@NgModule({
  imports: [
    RouterModule.forRoot(routes, { scrollPositionRestoration: 'enabled' }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
