import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-image-modal',
  templateUrl: './image-modal.component.html',
  styleUrls: ['./image-modal.component.scss']
})
export class ImageModalComponent implements OnInit {

  @Output() closePoup = new EventEmitter<boolean>();
  @Input() items :any;
  itemCount:number = 1
  constructor( private router: Router) { }
  ngOnInit(): void {    
  }

  cancelBtnClick(){
    this.closePoup.emit(false)
  }

  increament(Item:any):void {
   this.itemCount =  Item + 1;   
  }
  
  decreament(Item:any):void {
    this.itemCount = Item - 1;
  }

  public imageDetailedView():void {

    this.router.navigate(['home/imageDetailedView']);
  }
}
