import { Component, OnInit } from '@angular/core';
import { SharedCartService } from 'projects/e-commerce/src/app/sharedServices/cartDataService';
import { Router } from '@angular/router';

@Component({
  selector: 'app-search-nav',
  templateUrl: './search-nav.component.html',
  styleUrls: ['./search-nav.component.scss'],
})
export class SearchNavComponent implements OnInit {
  selectedMessage: any;

  constructor(
    private sharedCartService: SharedCartService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.sharedCartService.currentMessage.subscribe(
      (message: any) => (this.selectedMessage = message)
    );
    console.log(this.selectedMessage['anee'], 'this.selectedMessage');
  }

  public goToHome() {
    this.router.navigate(['/home']);
  }
}
